/* ================================ */

/* 格式化样式标签、基础类型样式		*/

/* ================================ */

html {
	font-size: 62.5%
}

@media only screen and (min-width: 481px) {
	html {
		font-size: 94%!important
	}
}

@media only screen and (min-width: 561px) {
	html {
		font-size: 109%!important
	}
}

@media only screen and (min-width: 641px) {
	html {
		font-size: 125%!important
	}
	body {
		/*max-width: 640px;
		margin: 0 auto !important;*/
	}
}

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, font, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td {
	margin: 0;
	padding: 0;
	border: 0;
	outline: 0;
	font-size: 100%;
	vertical-align: baseline;
	/*background: transparent;*/
}

body {
	/*line-height: 1;*/
	background-color: rgb(247, 247, 247);
	font-family: "Helvetica Neue",Helvetica,"PingFang SC","Hiragino Sans GB","Microsoft YaHei","\5FAE\8F6F\96C5\9ED1",Arial,sans-serif;
}

ol, ul {
	list-style: none;
}

blockquote, q {
	quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}

img {
	vertical-align: middle;
}

/* remember to define focus styles! */

:focus {
	outline: 0;
}

/* remember to highlight inserts somehow! */

ins {
	text-decoration: none;
}

del {
	text-decoration: line-through;
}

/* tables still need 'cellspacing="0"' in the markup */

table {
	border-collapse: collapse;
	border-spacing: 0;
}

/*去除浏览器默认黄色背景*/

input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
input:-webkit-autofill:active {
    -webkit-transition-delay: 9999s !important;
    -webkit-transition: color 9999s ease-out, background-color 9999s ease-out !important;
}

/*横向布局(默认1行2个)*/

.horizontal-layout {
    font-size: 0;
}

.horizontal-layout li {
	display: inline-block;
	width: 50%;
}

.mb5 {
	margin-bottom: 0.5rem;
}
.mint-swipe {
	height: 13.25rem;
}

.mint-swipe img {
	width: 100%;
	height: 100%;
}

.popupBar {
	width: 70%;
	height: 100vh;
}

.popupBar .person {
	text-align: center;
	padding: 10px 0;
}

.popupBar .person img {
	width: 80px;
	height: 80px;
	border-radius: 50%;
	border: 1px solid #efefef;
	margin-bottom: 0.3125rem;
}

.popupBar .person p {
	font-size: 0.875rem;
	color: #333;
}

.mint-tab-item .mint-badge {
	position: absolute;
	right: 20px;
	top: 4px;
}

.more-list {
	border-bottom: 1px solid #efefef;
}

.more-list:last-child {
	background-image: none;
}

.more-list .mint-cell-wrapper {
	font-size: 0.875rem;
	color: #26a2ff;
}

.index-list {
	font-size: 0;
}

.index-list .mint-cell {
	width: 50%;
	display: inline-block;
	border-bottom: 1px solid #efefef;
}

.index-list .mint-cell:nth-child(2n-1) {
	border-right: 1px solid #efefef;
	margin-right: -1px;
}

.index-list .mint-cell:nth-child(2n) {
	border-left: 1px solid #efefef;
	margin-left: -1px;
}

.index-list2 .mint-cell {
	border-bottom: 1px solid #efefef;
}

.mint-tab-container-item[type='index'] .mint-cell:last-child,
.mint-tab-container-item[type='index'] .mint-cell-wrapper {
	background-image: none;
}

/*member*/

.member-panel {
	height: 12.5rem;
	background-color: #fff;
	/*background-color: cornsilk;*/
	position: relative;
}

.member-panel .panel-head {
	text-align: center;
	padding-top: 1rem;
	padding: 1rem 0.625rem 0;
}

.member-panel .panel-head img {
	width: 80px;
	height: 80px;
	border-radius: 50%;
	border: 1px solid #efefef;
}

.member-panel .panel-head p {
	margin-top: 0.35rem;
	font-size: 0.875rem;
	color: #333;
	overflow: hidden;
	text-overflow: ellipsis;
	white-space: nowrap;
}

.member-panel .panel-foot {
	position: absolute;
	bottom: 0;
	width: 100%;
}

.member-panel .panel-foot p {
	width: 100%;
	text-align: center;
	padding: 0.35rem 0;
	margin: 1rem 0 0.7rem;
	border-right: 1px solid #eee;
}

.member-panel .panel-foot p:first-child {
	/*background-color: lavenderblush;*/
}

.member-panel .panel-foot p:nth-child(2) {
	/*background-color: bisque;*/
}

.member-panel .panel-foot p:nth-child(3) {
	border-right: none;
	/*background-color: lightgreen;*/
}

.member-panel .panel-foot p .fa-icon {
	display: block;
	margin: 0 auto 0.3125rem;
}

.member-panel .panel-foot p span {
	font-size: 0.875rem;
	color: #333;
}

.mt-grid {
	margin: 0.5rem 0;
	padding: 0.5rem;
	background-color: #fff;
}

.mt-grid .mt-grid-content {
	font-size: 0;
	overflow: hidden;
}

.mt-grid .mt-grid-content .mt-grid-content-item {
	display: inline-block;
	width: 33.3%;
	margin: -1px;
	border-right: 1px solid #efefef;
	border-bottom: 1px solid #efefef;
	padding: 1rem 0;
	text-align: center;
}

.mt-grid .mt-grid-content .mt-grid-content-item:nth-child(3n) {
	border-right: none;
}

.mt-grid .mt-grid-content .mt-grid-content-item p {
	font-size: 0.875rem;
	color: #333;
	padding: 0.5rem 0.5rem 0;
	white-space: nowrap;
	overflow: hidden;
	text-overflow: ellipsis;
}

.member-cell-group,
.member-cell {
	margin-top: 0.5rem;
}

.member-cell-group .member-cell {
	margin-top: 0;
}

.mint-popup-2 {
	width: 100%;
	height: 40px;
	text-align: center;
	background-color: rgba(0, 0, 0, .7);
	-webkit-backface-visibility: hidden;
	backface-visibility: hidden;
}

.mint-popup-2 p {
	line-height: 40px;
	color: #fff;
}